<template><section><html><head></head><body><h1>EllipseGeometry、 EllipseOutlineGeometry</h1>
<ul>
<li><code v-pre="">vc-geometry-ellipse</code> 组件用于加载椭圆。</li>
<li><code v-pre="">vc-geometry-outline-ellipse</code> 组件用于加载椭圆轮廓。</li>
<li>需要作为 <code v-pre="">vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre="">vc-primitive</code> 或 <code v-pre="">vc-primitive-ground</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载椭圆</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @left_click="LEFT_CLICK">
        <vc-primitive-ground :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-ellipse ref="ellipse" :center="center" :semi-minor-axis="200000.0" :semi-major-axis="300000.0" :height="50000"></vc-geometry-ellipse>
          </vc-instance-geometry>
        </vc-primitive-ground>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-outline-ellipse ref="ellipseOutline" :center="center" :semi-minor-axis="200000.0" :semi-major-axis="300000.0" :height="100000">
            </vc-geometry-outline-ellipse>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
