        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'All geometries are loaded.'</span>)
        <span class="hljs-keyword">const</span> boundingSphereUnion = instances.<span class="hljs-title function_">reduce</span>(<span class="hljs-function">(<span class="hljs-params">prev, cur</span>) =&gt;</span> {
          <span class="hljs-keyword">const</span> geometry = cur.<span class="hljs-property">cesiumObject</span>.<span class="hljs-property">constructor</span>.<span class="hljs-title function_">createGeometry</span>(cur.<span class="hljs-property">cesiumObject</span>)
          <span class="hljs-keyword">const</span> boundingSphere = cur.<span class="hljs-property">vm</span>.<span class="hljs-property">$parent</span>.<span class="hljs-property">modelMatrix</span>
